.pr-tab {
  width: 100%;
  height: 100%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  // border: 2px solid red;
  background-color: rgba(255, 255, 255);
  border-radius: 3px;
  overflow: hidden;
  .inner-pr-tab {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    gap: 7px;
    .tabs {
      width: 100%;
      height: fit-content;
      box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
      background-color: #dfdfde;
     
      display: flex;
      justify-content: flex-start;
      align-items: center;
     

      p {
        padding: 4px 0px;
       
        .rep-tab {
            height: fit-content;
          text-decoration: none;
          font-size: 0.7rem;
          color: rgb(71, 71, 71);
          padding: 5px 7px;
         
          
         
        }

        .active {
           
            height: fit-content;
          background-color: white;
          padding: 8px 7px;
          font-weight: 500;
         
        }
      }
    }

    .stats-outlet{
        width: 100%;
        height: 100%;
       
    }
  }
}
