


.stepss{
   width: 100%;
   height: 100%;
   
   
    .inner_steps{
       width: 100%;
       height: 100%;
       display: flex;
       justify-content: flex-start;
       align-items: center;
       flex-direction: column;
       gap: 10px;
      //  padding: 10px;
       overflow-x: hidden;
       .step_heading{
         width: 100%;
         height: 30px;
         padding: 4px 0px;
         display: flex;
         justify-content: space-between;
         align-items: center;
         

        p{
         font-size: 0.9rem;
         font-weight: 500;
         width: 100%;
         color: #3f4d67;
        
         font-weight: 500;
        }
        button{
         padding: 5px;
         font-size: 0.8vw;
         background-color: #2f63c7;
         border: none;
      color: white;
      border-radius: 3px;
         width: 80px;
        }
       
      }

       .test_step_table{
      
        width: 100%;
         height: calc(100% - 50px);
         // border: 2px solid red;
         overflow: scroll;
         display: flex;
         justify-content: flex-start;
         align-items: center;
         flex-direction: column;
         gap: 10px;
         //  border: 2px solid red;
         .steps{
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: flex-start;
            max-height: 300px;
            min-height: 100px;
            // border: 2px solid red;
            gap: 10px;
            // border: 2px solid red;
             position: relative;
            .step-number{
            width:4%;
            min-height: 100px;
            background-color: #5d7198;
            border-radius: 3px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-direction: column;
            align-self: stretch;

            .num{
               p{
                  padding: 4px 6px;
                  color: white;
                  font-size: 0.8rem;
                  color: #d2dbe4;
                  font-weight: 500;
               }
            }

            }
            .test-action{
               width: 32%;
                min-height: 100px;
                border: 1.5px solid #eceef0;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                flex-direction: column;
                gap: 7px;
                border-radius: 3px;
                align-self: stretch;
               
            }

            .test-data{
             width: 32%;
                min-height: 100px;
                border: 1.5px solid #eceef0;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                flex-direction: column;
                gap: 7px;
                border-radius: 3px;
                align-self: stretch;
            }

            .step-option{
               position: absolute;
               align-self: stretch;
               right: 10px;
               top: 5px;
               height: 100%;
               display: flex;
               justify-content: flex-start;
               align-items: center;
               flex-direction: column;
               
               .options-box{
                  
                  position: absolute;
                  top: 15px;
                  right: -3px;
                  border-radius: 1px;
                  
                  overflow: hidden;
                  p{
                     color:#485567;
                     width: 100%;
                     padding: 3px 8px;
                     font-size: 0.6rem;
                     background-color: #eceef0;
                  }
                  p:hover{
                     background-color: #2f63c7;
                     color: white;
                     cursor: pointer;
                  }
               }
              
            }
           

            .expected-result{
             max-width: 32%;
             min-width: 32%;
                min-height: 100px;
                border: 1.5px solid #eceef0;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                flex-direction: column;
                gap: 7px;
                border-radius: 3px;
               
            }

            .column-name{
               width: 100%;
               color: #d2dbe4;
               font-size: 0.7rem;
               padding: 3px 6px;
            }

            .step-content{
               width: 100%;
               height: fit-content;
               padding: 0px 10px 7px 10px;
              
               // border: 2px solid red;
               // overflow-y: scroll;
               p{ 
                  width: 100%;
                  height: 100%;
                  font-size: 0.8rem;
                  color:#485567;
                
               }
            }
         }

         .create-step-comp{
            // border: 2px solid red;
            height: fit-content;
            width: 100%;
         }


       }
    }
}

.test_step_table::-webkit-scrollbar{
   display: none;
}




@media screen and (max-width:2560px) and (min-width:1920px) {
   
   .stepss{
    

      .inner_steps{

         .test_step_table{
            
            .column-name{
              
               font-size: 0.8rem;
            }
         }
      }
   }
}