.linechartinterface{
    width: 100%;
    height: 100%;

    .innerlinechartinterface{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        gap: 10px;
        
        .chartbox{
            width: 100%;
            border-radius: 4px;
            background-color: white;
         
            box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
            .test_name{
                padding: 5px;
                width: 100%;
                text-align: center;
                background-color: rgb(234, 234, 234);
            }

            .innerchartbox{
                width: 100%;
                height: 400px;
            }
        }
    }
}