// $background_color:#124076;
// $lighter_shade:#fcf4f7;
$background_color: #5d5fef;
$lighter_shade: #313e4e;
$background_shade: #fafbfc;


@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300..700&display=swap');



.navbar{
    width:100%;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0px;
    
    background-color: $lighter_shade;
    box-shadow: rgba(76, 76, 76, 0.12) 0px 1px 3px,
    rgba(152, 152, 152, 0.24) 0px 1px 2px;
   
    top: 0px;
    left: 0px;
   

    .inner_navbar{

        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
       
        //  border: 2px solid red;
        padding: 0px 10px;
        .logo_section{
          
            width: fit-content;
            height: 100%;
            
            display: flex;
            justify-content: flex-start;
            align-items: center;
            gap: 10px;
            // .img_logo{
            //     width: fit-content;
            //     height: 100%;
            //     display: flex;
            //     justify-content: center;
            //     align-items: center;
            //     img{
                
            //         width: fit-content;
            //         height: 100%;
            //         object-fit: cover;
            //     }
            // }
            .portal_name{
                width: fit-content;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                 .name-portal{
                     padding: 5px;
                    height: 100%;
                    color: $background_color;
                    font-family: "Comfortaa", sans-serif;
                    cursor: pointer;
                   
                    font-size: 1.7vw;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-weight: 1000;
                   
                 } 
                
            }

            .top_level_option{
                width: fit-content;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 10px;
                color: white;
                padding: 0px 10px;
                font-size: 1vw;
             
                
                p{
                    width: 100%;
                    padding: 4px 7px;
                    text-decoration: none;
                    .links{
                        text-decoration: none;
                        color: #cac3d9;
                        font-size: 1vw;
                        font-weight: 400;
                        // border: 2px solid red;
                      
                    }
                    .active{
                        width: 100% ;
                        height: 100%;
                        padding: 4px 7px;
                         border-radius: 3px;
                        // background-color: $background_color;
                        // color: white;
                        background-color: #475a74;
                    }
                }
            }

            
           
        }
       

        .profile_and_logout_section{
            width: fit-content;
            height: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            gap: 13px;
           
            .profile_section{
                 height: 35px;
                 width: 35px;

                 display: flex;
                 justify-content: center;
                 align-items: center;
                 border-radius: 50%;
                 cursor: pointer;
                 padding: 2px;
             


                .inner_profile_section{
                    width: 100%;
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    img{
                      width: 100%;
                      height: 100%;
                      object-fit: contain;
                      cursor: pointer;
                    }
                }

              
            }

            .profile_section:hover{
                box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
            }



            .logout_button{
                width: 35px;
                height: 35px;
               
                display: flex;
                justify-content: center;
                align-items: center;

                .logout_icon{
                   font-size: 1.5rem;
                   color: $lighter_shade;
                }
            }


        }


    }
}


@media screen and (max-width:2559px) and (min-width:1920px) {
    .navbar{
        height: 70px;
       
    }
}

@media screen and (min-width:2560px) {
    .navbar{
        height: 70px;
    }
}