.report_table{
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    border-radius: 4px;
    overflow: hidden;
    .inner_table{
        table{
            
            width: 100%;
            // box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
            border-collapse:collapse ;
            border-color: rgba(188, 188, 188, 0.525);
            thead{
               
                #one{
                 padding: 10px;
                 background-color: whitesmoke;
                   th{
                      padding: 8px 5px;
                      font-size: 0.65rem;
                      font-weight: 500;
                   } 
                }
                #two{
                    th{
                        padding: 4px 5px;
                        font-size: 0.65rem;
                        font-weight: 450;
                    }
                }
            }
            tbody{
                border-collapse:collapse ;
                tr{
                    .grey{
                       background-color: greenyellow;
                    }
                    .black{
                       background-color: white;
                    }
                   th{
                    padding: 5px 5px;
                   }
                   td{
                    padding: 5px 5px;
                    font-size: 0.7rem;

                   }
                }
            }
        }
    }
}