// App.scss
$background_color: #5d5fef;
$lighter_shade: #ffffff;
$background_shade: #fafbfc;
.report-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;

  .overview-top{
    width: 100%;
    height: 12%;
    // border: 2px solid red;
    box-shadow: rgba(76, 76, 76, 0.12) 0px 3px 3px -3px, rgba(152, 152, 152, 0.24) 0px 2px 2px -2px;
    padding: 10px 20px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color:$lighter_shade ;
    h4{
        color: #151D48;
        font-weight: 600;
    }

}

  .content-box {
    
     padding: 20px 15px;
    
    width: 100%;
    height: 88%;
    .inner-content-box{
      height: 100%;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      border-radius: 4px;
      gap: 0px;
      background-color: $lighter_shade;
      box-shadow: rgba(76, 76, 76, 0.12) 0px 1px 3px, rgba(152, 152, 152, 0.24) 0px 1px 2px;
    .report_menu{
      box-shadow: rgba(76, 76, 76, 0.12) 0px 3px 3px -3px, rgba(152, 152, 152, 0.24) 0px 2px 2px -2px;
      padding: 0px 20px;
      width: 100%;
      background-color: white;
      // border: 2px solid red;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 10px;
     
        .rep_menu{
          text-decoration: none;
          color: #B1BCD1;
        
          padding: 12px;
          // padding: 7px 3px;
          p{
            font-size: 0.75rem;
            font-weight: 600;
          }
        }
        .active{
          border-bottom: 2px solid $background_color;
          color: $background_color;
        }
      
    }

    .report-outlet{
      width: 100%;
      height: calc(100% - 45px);
      // border: 2px solid red;
      padding: 10px;
     
    }
  }
  }

}
