// App.scss
.container {
    width: 100vw;
    height: 100vh;
    display: flex;
    transition: all 0.3s ease;
    border: 2px solid red;
  
    .side-panel {
      width: 20px;
      overflow: hidden;
      transition: all 0.3s ease;
      position: relative;
  
      button {
        position: absolute;
        top: 10px;
        right: -5px;
        padding: 10px;
        margin: 10px;
        z-index: 10000;
      }
    }
  
    .content-box {
      flex: 1;
      padding: 20px;
      background-color: #f0f0f0;
      transition: all 0.3s ease;
    }
  
    &.open {
      .side-panel {
        width: 200px;
        
      }
  
      .content-box {
        margin-left: 200px;
      }
    }
  }
  