.custom-dropdown{
    width: 100%;
   
    position: relative;
    .dropdown-header{
        padding: 6px 15px;
        border: 1px solid black;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;
        font-weight: 500;

    }
    .dropdown-list{
        position: absolute;
        left: 0px;
        border: 1px solid black;
        border-top: none;
         min-width: 100%;

        span{
             list-style: none;
             display: block;
             padding: 6px 15px;
        }
    }
}