.natco_table_test_level::-webkit-scrollbar { 
  display: none;  /* Safari and Chrome */
}


.natco_table_test_level {
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;
  width: 100%;
  border: 2px solid red;
  overflow: hidden;
  height: 100%;
  padding: 10px;
  .inner_natco_table {
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    padding: 10px;
    overflow: hidden;
    .natco_heading {
      width: 100%;
      height: fit-content;
      padding: 4px 0px;
      border-bottom: 1px solid black;
    }

    .natco_table_content {
      width: 100%;
      height: fit-content;
      table {
        width: 80%;
        border-collapse: collapse;
        
        tbody {
          .column_name{
            th{
               padding: 5px 10px;
               font-size: 1.2vw;
               font-weight: 600;
            }
          }
           .row_black{
            border-collapse: collapse;

            th {
              border: 1px solid black;
              padding: 5px 10px;
              font-weight: 400;
              font-size: 1vw;
               color: black;
               color: rgb(37, 37, 37);
              
             
            }
            #applicable{
              
             color: black;
            }

          
          }

          .row_grey{
            border-collapse: collapse;

            th {
              border: 1px solid black;
              padding: 5px 10px;
              font-weight: 400;
              font-size: 1vw;
              background-color: grey;
              color: rgb(37, 37, 37);
             
            }
          }
          
        }
      }
    }
  }
}
