$background_color: #5d5fef;
$lighter_shade: #ffffff;
$background_shade: #fafbfc;

.excel-modal{
    position: absolute;
   
    width: 100%;
    height: 100%;
    // border: 2px solid red;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 10;
  background-color: rgba(0, 0, 0, 0.187);

  display: flex;
  justify-content: center;
  align-items: center;
  

  .inner-excel-modal{
    width: 500px;
    height: 250px;
    // border: 2px solid red;
    border-radius: 5px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    position: relative;
    padding: 15px;
    .close-icon{
       position: absolute;
       right: 10px;
       top: 8px;
       cursor: pointer;
    }
    .report-button-tab{
        height: 10%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 5px;
        .upload-tab{
            font-size: 0.8rem;
            font-weight: 500;
            padding: 4px 8px;
            color: #525b6c;
            cursor: pointer;
        }
    }
    .upload-section{
        width: 100%;
       
        height: 90%;
        padding: 30px 0px 5px 0px;
       
    }
  }

}

.close-m{
    display: none;
}