.errorpage{
    width: 100%;
    height: 100vh;
    

    .inner_error_page{
         width: 100%;
         height: 100%;
         display: flex;
         justify-content: center;
         align-items: center;
         flex-direction: column;
         gap: 20px;
         h1{
            font-size: 5vw;
         }
         h3{
            font-size: 1.5vw;
            color: rgb(46, 46, 46);
         }
         button{
            background-color: rgb(209, 209, 209);
                padding: 8px 12px;
                font-size: 1vw;
                border-radius: 3px;
                border: none;
                cursor: pointer;
                box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
         }
    }
}