.create-step {
    width: 100%;
    height: fit-content;
 
  }
  
  .inner-create-step {
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: stretch; /* Stretch items to match the height */
    gap: 10px;
  }
  
  .c-step-number {
    width: 4%;
    // background-color: #5d7198;
    background-color: #607593;
    border-radius: 3px;
    display: flex;
    justify-content: flex-start; /* Center content horizontally */
    align-items: center;
    flex-direction: column;
    padding: 10px;
    color: white;
    align-self: stretch; /* Stretch to fill the parent's height */
    font-size: 0.9rem;
  }
  
  .actions {
    width: 96%;
    background-color: rgb(235, 240, 245);
    height: fit-content;
    padding: 10px;
    border-radius: 3px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
    flex-direction: column;
  }
  
  .c-create-action,
  .c-create-data,
  .c-create-expected-result {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 7px;
    // border: 2px solid red;
  }
  
  .c-create-action p,
  .c-create-data p,
  .c-create-expected-result p {
    width: 100%;
    color: #3f4d67;
    font-weight: 500;
    font-size: 0.9rem;
  }
  
  .c-create-action .input,
  .c-create-data .input,
  .c-create-expected-result .input {
     cursor:text;
    width: 100%;
    padding: 5px 10px;
    border-radius: 3px;
    border: none;
    background-color: rgb(244, 246, 248);
  }


  .add-step-button{
    width: 100%;
   display: flex;
   justify-content: flex-end;
   align-items: center;
   gap: 10px;
    text-align: right;
    button{
        padding: 4px 8px;
        background-color: #2f63c7;
        border: none;
        color: white;
        border-radius: 3px;
        font-size: 0.8vw;
        cursor: pointer;
    }
  }
  