$border-color: #35374b;
$first: #861a49;
$second: hsl(334, 77%, 46%);
$third: #a81b58;
$lighter-shade:hsla(334, 78%, 58%, 0.687);
.stepInfo {
  width: 100%;
  height: fit-content;
//   border: 1px solid $border-color;
  display: flex;
  justify-content: center;
  align-items: center;

  .inner_step_info {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    border: 2px solid $border-color;
    border-radius: 5px;
    background-color: white;
    position: relative;
    .step_number {
      width: 6%;
      height: 100%;
      border-radius: 5px;
    //   background-color: $lighter-shade;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 5px 10px;
      font-weight: 600;
      
    }
    .test_data{
      width: 24%;
      border-radius: 5px;
    //   background-color: $lighter-shade;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 5px 10px;
      font-weight: 600;
    }
    .test_step {
      width: 35%;
      height: 100%;
      padding: 5px;
      text-align: start;
    //   border: 1px solid $border-color;
    }
    .test_step_expected_result {
      width: 35%;
      height: 100%;
      padding: 5px;
    //   border: 1px solid $border-color;
    }

    .dots{
       position: absolute;
       width: 15px;
       height: 15px;
       background-color: whitesmoke;
       border: 1px solid $second;
       border-radius: 50%;
       right: 49.6%;
       bottom: -12px;
       display: flex;
       justify-content: center;
       align-items: center;
    


       .inner_dot{
        display: inline-block;
        background-color: $first;
       border-radius: 50%;
       width: 70%;
       height: 70%;
       }
    }
  }
}
