$background_color: #5d5fef;
$lighter_shade: #ffffff;
$background_shade: #fafbfc;
.upload_report {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  cursor: pointer;

  gap: 10px;
  flex-direction: column;

  form {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .label_excel {
      height: 100%;
      width: 100%;
      // border: 2px solid red;
      //  padding: 0px 10px ;
      padding: 5px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: grey;
      font-size: 1.2vw;
      background-color: $background_shade;
      border: 1px dashed $background_color;
      cursor: pointer;
      border-radius: 5px;
      padding: 10px;

      .label-upload {
        width: 100%;
        height: 100%;
        // border: 2px solid red;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
         position: relative;
         .file-name{
            position: absolute;
            left: 0px;
            top: 0px;
            display: flex;
            justify-content: center;
            align-items: center;
            p{
              padding: 4px 10px;
              border-radius: 20px;
              background-color: $background_color;
              color: #ffffff;
              font-size: 0.6rem;
            }
          }

        .icon-upload {
          font-size: 1.9rem;
        }
        h4 {
          font-size: 0.8rem;
          font-weight: 600;
          color: $background_color;
          margin-bottom: 6px;
        }
        p {
          font-size: 0.65rem;
          font-weight: 500;
        }
      }
    }
    .excel_files {
      display: none;
    }
  }

  .upload-report-button {
   
    width: 100%;
    height: 20%;
    text-align: right;
    position: relative;
   
    button {
      width: fit-content;
      height: 100%;
      padding: 8px 15px;
      border: none;
      border-radius: 3px;
      // background-color: rgb(209, 209, 209);
      background-color: $background_color;
      cursor: pointer;
      color: white;
     
    }
  }
}
