
$background_color: #5d5fef;
$lighter_shade: #ffffff;
$background_shade: #fafbfc;
.add-test-case-modal{
    position: absolute;
    width: 100%;
    height:100%;
    z-index: 100;
    top: 0px;
    // border: 2px solid red;
    background-color: #313e4e4c;
    backdrop-filter: blur(2px);
    display: flex;
    justify-content: center;
    align-items: center;
    
    
    .inner-form-one{
          width: 40%;
          height: 370px;
        background-color: white;
        border-radius: 5px;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        // padding: 15px;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        .heading-test-modal{
            width: 100%;
            height: 40px;
            // border: 2px solid red;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0px 10px;
            background-color: $background_shade;
            box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
            p{
                font-size: 0.8rem;
                font-weight: 600;
            }
        }
        .form-test-modal{
            width: 100%;
            height: calc(100% - 80px);
            padding: 15px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-direction: column;
            gap: 10px;
            .form-group{
                // border: 2px solid red;
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: flex-start;
                flex-direction: column;
                gap: 5px;
                label{
                    font-size: 0.8rem;
                    font-weight: 600;
                    color:#525b6c;
                }
                input{
                    box-shadow: #dae1e9 0px 0px 1px 0px, #dae1e9 0px 0px 2px 0px;
                    width: 100%;
                    border: 1px solid #dae1e9;
                    border-radius: 2px;
                    padding: 8px;
                    font-size: 0.8rem;
                    color: #485567;
                }
                textarea{
                    box-shadow: #dae1e9 0px 0px 1px 0px, #dae1e9 0px 0px 2px 0px;
                    width: 100%;
                    border-radius: 3px;
                    padding: 5px;
                    color: #485567;
                    font-size: 0.8rem;
                    border: 1px solid #dae1e9;
                  }
                  textarea:focus{
                      border: 1px solid rgb(93, 114, 144);
                      box-shadow: rgb(93, 114, 144) 0px 0px 2px 0px, rgb(93, 114, 144)  0px 0px 1px 0px;
                      outline: none;
                  }
                input:focus {
                    outline: none;
                    border: 1px solid #737791; /* Add your preferred border style */
                    box-shadow: 0 0 2px #737791; /* Optional: Add a shadow effect */
                  }
            }
            .drop-down-type-and-status{
                width: 100%;
                // border: 2px solid red;
                margin-top: 10px;
                display: flex;
                justify-content: flex-end;
                gap: 20px;
                align-items: center;
                flex-direction: row-reverse;
                .form-group-drop{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    // border: 2px solid red;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    
                    label{
                        font-size: 0.8rem;
                        font-weight: 600;
                        color:#525b6c;
                    }
                }
            }
        }
        .next-button{
            width: 100%;
            height: 40px;
            // border: 2px solid red;
            text-align: right;
            padding: 0px 15px;
            button{
                padding: 6px 10px;
                font-size: 0.8rem;
                font-weight: 500;
                // border: 1px solid $background_color;
                border-radius: 3px;
                border: none;
                background-color: $background_color;
                color: white;
                cursor: pointer;
            }
        }

    }

    .inner-form-two{
        width: 85%;
        height: 500px;
        background-color: white;
        border-radius: 5px;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        // padding: 15px;
        overflow: hidden;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        gap: 20px;

        .heading-test-modal{
            width: 100%;
            height: 40px;
            // border: 2px solid red;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0px 10px;
            background-color: $background_shade;
            box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
            p{
                font-size: 0.8rem;
                font-weight: 600;
            }
        }

        .form-test-modal{
            padding: 15px;
            height: calc(100% - 80px);
            .steps{
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: space-between;
                align-items:center;
                flex-direction: column;

                table{
                    width: 100%;
                    max-height: calc(100% - 100px);
                    
                    border-collapse: collapse;
                    // margin-bottom: 20px;
                    border-color: #e2eaf0;
                    // border-spacing: 0 1em;
                    box-shadow: #e2eaf0 0px 1px 3px 0px, #e2eaf0 0px 0px 0px 1px;
                    border-radius: 2px;
                    overflow: scroll;
                   
                    thead{
                        width: 100%;
                        border: 1px solid #e2eaf0;
                        box-shadow: #e2eaf0 0px 1px 3px 0px, #e2eaf0 0px 0px 0px 1px;
                        tr{
                            font-size: 0.8rem;
                            border-radius: 5px;
                            background-color: #f9fafc;
                           
                            th{
                                padding: 8px;
                                color: #151d48;
                                font-weight: 600;
                                // border-right: 1px solid white;
                                word-wrap: break-word; /* Allow breaking long words */
                                white-space: normal;
                            }
                        }
                    }

                    tbody{
                        margin: 3px 0px;
                        border-color: #e2eaf0;
                        width: 100%;
                        tr{
                            margin: 10px;
                            td{
                                color:#525b6c;
                                font-size: 0.8rem;
                                font-weight: 600;
                                padding: 6px 10px;
                                word-wrap: break-word; /* Allow breaking long words */
                                white-space: normal;
                            }
                        }
                    }
                    // tbody:before {
                    //     line-height:1em;
                    //     content:"-";
                    //     color:white; /* to hide text */
                    //     display:block;
                    // }
                }

                .add-step{
                    height:70px;
                    // border: 2px solid red;
                    // background-color: #5d5fef;
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    align-items:center;
                    margin-top: 15px;
                    // border: 1px solid #e2eaf0;
                    padding: 5px;
                    box-shadow: #e2eaf0 0px 1px 3px 0px, #e2eaf0 0px 0px 0px 1px;
                   
                    textarea{
                        margin: 5px;
                        padding: 5px;
                        border: 1px solid #ccc;
                        padding: 10px;
                        border-radius: 4px;
                        outline: none;
                        height: 100%;
                    }
                    textarea:focus {
                        border-color: #737791; /* Change border color */
                        box-shadow: 0 0 2px #737791; /* Add shadow */
                      }
                    button{
                        margin: 2px;
                        border: none;
                        background-color: #dbdfed;
                        padding: 5px;
                        border-radius: 5px;
                        color: #151d48;
                        cursor: pointer;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        // border: 2px solid red;
                        height: 100%;
                        
                    }

                }

                .add-testcase-button{
                    button{
                        background-color: #5d5fef;
                        color: white;
                        border-radius: 5px;
                    }
                }

            }
        }

        .next-button{
            width: 100%;
            height: 50px;
            // border: 2px solid red;
            text-align: right;
            padding: 0px 15px;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            gap: 10px;
            // border: 2px solid red;
            button{
                padding: 6px 10px;
                font-size: 0.8rem;
                font-weight: 500;
                border: 1px solid $background_color;
                border-radius: 3px;
                background-color: white;
                color: $background_color;
                cursor: pointer;
            }
        }

    }
}