/* App.css */

.editor-container {
    width: 100%;
    // border: 2px solid red;
    margin: 0 auto;
     
    border: 1px solid #ddd;
  
  }
  
  .ck-editor__editable {
    min-height: 120px; /* Set a minimum height for the editor */
    padding: 50px;
    border: 2px solid red;
  }

  .ck-content {
    padding: 50px; /* Adjust padding as needed */
  }

  .ck-editor__editable_inline {
    padding: 10px; /* Adjust padding as needed */
  }
  
  /* For classic editor, use this selector if necessary */
  .ck-editor__editable {
    padding: 10px; /* Adjust padding as needed */
  }
  
  .ck-toolbar .ck-button .ck-icon {
    font-size: 8px; /* Adjust size as needed */
  }