$background_color: #5d5fef;
$lighter_shade: #313e4e;
$background_shade: #fafbfc;
.reportInterface {
  width: 100vw;
  height: 100vh;

  .innerInterface {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 0px;

    .first_level {
      padding: 0px 10px;
      // width: 15%;
      height: 100%;
      transition: all ease-in-out 1s;
      z-index: 10;
      background-color: $lighter_shade;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 3px;
      box-shadow: rgba(76, 76, 76, 0.12) 0px 1px 3px,
        rgba(152, 152, 152, 0.24) 0px 1px 2px;
      flex-direction: column;

      .logo_name {
        height: 12%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        // padding: 20px;

        .inner-logo-name {
        
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          position: relative;
       
          // gap: 20%;
          .left-logo {
            display: flex;
            justify-content: flex-start;
            align-items: center;
           
            gap: 10px;
            h3 {
              color: #151d48;
            }
            span {
              width: 35px;
              height: 35px;
              display: flex;
              justify-content: center;
              align-items: center;
              color: white;
              font-size: 1.2rem;
              background-color: $background_color;
              border-radius: 5px;
              img {
                width: 60%;
                height: 60%;
                object-fit: contain;
              }
            }
          }

          .hamburger {
            position: absolute;
            right: 0px;
         

            // width: 100%;
            // height: 100;
            // position: absolute;
          }
        }
      }

      .type_option {
        width: 100%;
        height: 88%;

        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        padding: 10px;
        gap: 15px;
        .type-button {
          width: 100%;

          .links {
            display: inline-block;

            border-radius: 5px;
            width: 100%;
            height: 100%;
            text-decoration: none;

            .button-menu {
              height: 40px;
              display: flex;
              justify-content: flex-end;
              align-items: center;

              position: relative;
              span {
                width: 30%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                color: #737791;
                position: absolute;
                left: 0px;
              }

              p {
                width: 70%;
                height: 100%;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                font-size: 0.8rem;
                font-weight: 500;
                color: #737791;
              }
            }
          }
          .active {
            background-color: $background_color;
            box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
              rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
            .button-menu {
              span {
                color: white;
                font-size: 1rem;
              }
              p {
                color: white;
                font-weight: 600;
              }
            }
          }
        }

        .close-change {
          .links {
            .button-menu {
              border-radius: 50%;
              // transition: border-radius 2s ease-in;
              // box-shadow: rgba(0, 0, 0, 0.04) 0px 3px 5px;
              span {
                width: 100%;
              }

              p {
                opacity: 0;
              }
            }
          }

          .active {
          }
        }

        .open-change {
          .links {
            .button-menu {
              span {
              }

              p {
                opacity: 1;
                transition: opacity 1s ease-in;
              }
            }
          }

          .active {
          }
        }
      }
    }

    .open {
      width: 200px;
      .logo_name {
        padding: 10px 10px;

        .left-logo {
          opacity: 1;
          transition: opacity 3s ease-in-out;
        }
        .hamburger {
          height: 100%;
          right: 0px;
          //  border: 2px solid red;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
    }
    .close {
      width: 70px;
     
      padding: 0px;
      .logo_name {
         padding: 10px;
        .inner-logo-name {
           width: 100%;
           justify-content: center;
           align-items: center;
      
          //  overflow: hidden;
          .left-logo {
            opacity: 0;
            // display: none;
          
          }
          .hamburger {
         
             padding: 0px;
             margin: 0px;
            display: flex;
            justify-content: center;
            align-items: center;
            right: 0px;
           
          }
        }
      }
    }
    .section {
     
      transition: all ease-in-out 1s;
      height: 100%;
      background-color: $background_shade;
    }

    .min {
      width: calc(100% - 200px);
    }
    .max {
      width: calc(100% - 70px);
    }
  }
}
