.testCase {
  width: 100%;
  height: 100%;
  //   border: 2px solid red;
  padding: 0px 5px;

  .inner_testcase {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    .top_menu_stats {
      width: 100%;
      // border: 2px solid red;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .graph-table {
        flex: 1;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        .tabular {
          .button {
            background-color: white;
            cursor: pointer;
            padding: 4px 8px;
            font-size: 0.7rem;

            border: none;
            box-shadow: rgba(9, 30, 66, 0.25) 0px 1px 1px,
              rgba(9, 30, 66, 0.13) 0px 0px 1px 1px;
          }

          .active {
            background-color: #dfdfde;
            padding: 4px 8px;
            font-size: 0.7rem;
            border: none;
            box-shadow: rgba(9, 30, 66, 0.25) 0px 1px 1px,
              rgba(9, 30, 66, 0.13) 0px 0px 1px 1px;
          }
        }

        .graph-form {
          .button {
            background-color: white;
            cursor: pointer;
            padding: 4px 8px;
            font-size: 0.7rem;
            border: none;
            box-shadow: rgba(9, 30, 66, 0.25) 0px 1px 1px,
              rgba(9, 30, 66, 0.13) 0px 0px 1px 1px;
          }
          .active {
            background-color: #dfdfde;
            padding: 4px 8px;
            font-size: 0.7rem;
            border: none;
            box-shadow: rgba(9, 30, 66, 0.25) 0px 1px 1px,
              rgba(9, 30, 66, 0.13) 0px 0px 1px 1px;
          }
        }
      }
      .thresh_filter {
        flex: 1;
        display: flex;
        justify-content: end;
        align-items: center;
        gap: 5px;
        label {
          font-size: 0.7rem;
        }
        input {
          font-size: 0.7rem;
          width: 40px;
          outline: none;
        }
      }
    }

    .bottom_display {
      width: 100%;
      height: 100%;
      overflow: scroll;
    }
    .bottom_display::-webkit-scrollbar{
      width: 4px;
    }
  }
}
