$background_color: #5d5fef;
$lighter_shade: #313e4e;
$background_shade: #fafbfc;
.homeInterface {
  width: 100vw;
  height: 100vh;

  .innerhomeInterface {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 0px;

    .first_level-h {
      padding: 0px 10px;
     
      height: 100%;
      transition: all ease-in-out 1s;
      z-index: 10;
      background-color: #313e4e;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 0px;
      box-shadow: rgba(76, 76, 76, 0.12) 0px 1px 3px,
        rgba(152, 152, 152, 0.24) 0px 1px 2px;
      flex-direction: column;
     

      .logo_name {
        height: 45px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
       
       

        .inner-logo-name {
         
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          position: relative;
         
          .hamburger {
           width: 100%;
           height: 100%;
           span{
           width: 100%;
           height: 100%;
           img{
           width: 100%;
           height: 100%;
           object-fit: contain;
          }
        }
        }
        }
      }

      .type_option {
        width: 100%;
        height: calc(100% - 45px);
       
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        padding: 10px 5px;
        gap: 15px;
      
        .type-button-h {
          width: 100%;
        
         

            .button-menu {
              height: 40px;
              display: flex;
              justify-content: flex-end;
              align-items: center;
             
              position: relative;
              span {
                width: 30%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                color: #cac3d9;
                position: absolute;
                left: 0px;
             
                font-size: 1.3vw;
              }

              p {
                width: 70%;
                height: 100%;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                font-size: 0.9vw;
                font-weight: 500;
                color: #737791;
               
              }
            }
          
          // .button-menu:hover {

          //   box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
          //     rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
          //   color: $background_color;
          // }
        }

        .type-button-h:hover{
          border-radius: 3px;
        //  background-color: $lighter_shade;
        //  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
        background-color: #475a74;
          .button-menu{
            cursor: pointer;
            span{
              color: #cac3d9;
            }
            p{
              color: $background_color;
            }
          }
         
        }

        .close-change {
         
            //  border: 2px solid red;
            .button-menu {
              border-radius: 50%;
              // transition: border-radius 2s ease-in;
              // box-shadow: rgba(0, 0, 0, 0.04) 0px 3px 5px;
              span {
                width: 100%;
              }

              p {
                opacity: 0;
              }
            }
          

          .active {
          }
        }

        .open-change {
          
            .button-menu {
              span {
              }

              p {
                opacity: 1;
                transition: opacity 1s ease-in;
              }
            }
          

          .active {
          }
        }
      }
    }

   
    .close {
      width: 45px;
     
      padding: 0px;
      .logo_name {
         padding: 10px;
        .inner-logo-name {
           width: 100%;
           justify-content: center;
           align-items: center;
      
          //  overflow: hidden;
          .left-logo {
            opacity: 0;
            // display: none;
          
          }
          .hamburger {
            
             padding: 0px;
             margin: 0px;
            display: flex;
            justify-content: center;
            align-items: center;
            right: 0px;
          
            span{
              width: 100%;
               img{
               }
            }
          }
        }
      }
    }
    .section {
     
      transition: all ease-in-out 1s;
      height: 100%;
      background-color: $background_shade;
      // border: 2px solid red;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
      // gap: 5px;
    }

    .min {
      width: calc(100% - 200px);
    }
    .max {
      width: calc(100% - 45px);
    }
  }
}


@media screen and (max-width:2559px) and (min-width:1920px) {
  .homeInterface{
    .innerhomeInterface{

      .first_level-h{
         gap: 15px;
        //  border: 2px solid red;
        .logo_name{
          height: 70px;
         
          .inner-logo-name{
          .hamburger{
            padding: 4px;
          }
          }
        }
      }
      .open-h{
        width: 250px;
      }
      .close{
      width: 65px;
      }
      .min {
        width: calc(100% - 250px);
      }
      .max{
         width: calc(100% - 65px);
      }
    }
  }
}

@media screen and (min-width:2560px) {
  .homeInterface{
    .innerhomeInterface{

      .first_level-h{
         gap: 20px;
        //  border: 2px solid red;
        .logo_name{
          height: 70px;
         
          .inner-logo-name{
          .hamburger{
            padding: 4px;
          }
          }
        }
      }
      .open-h{
        width: 250px;
      }
      .close{
      width: 70px;
      }
      .min {
        width: calc(100% - 250px);
      }
      .max{
         width: calc(100% - 70px);
      }
    }
  }
}