$background_color: #5d5fef;
$lighter_shade: #ffffff;
$background_shade: #fafbfc;
.page {
  width: 100%;
  height: calc(100% - 45px);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: $background_shade;
  position: relative;
  overflow-x: hidden;
  font-size: 1vw;

  .list {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;

    .inner_list {
      // padding-top: 50px;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;

      .filter_button {
        height: 45px;
        width: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        padding: 0px 15px;
        gap: 8px;
        // border: 2px solid red;
        position: relative;
        // border: 2px solid red;
        margin-top: 4px;

        button {
          padding: 7px 12px;
          border: none;
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 5px;
          background-color: white;
          box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
          border-radius: 4px;
          p {
            font-size: 0.9vw;
            font-weight: 600;
            color: #737791;
            font-family: system-ui, -apple-system, BlinkMacSystemFont,
              "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
              "Helvetica Neue", sans-serif;
          }
        }

        .filter-popup {
          position: absolute;
          top: 40px;
          display: flex;
          width: fit-content;
          align-items: center;
          justify-content: center;
          // border: 2px solid red;
          padding: 15px;
          border-radius: 5px;
          flex-direction: column;
          background-color: white;
          // box-shadow: #5d5fef 0px 0px 3px 0px,
          //   #5d5fef 0px 0px 0px 0px;
          box-shadow: #e5ebef 0px 0px 3px 2px;
          gap: 25px;
          .filter-options {
            width: fit-content;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 20px;

            .dropdown {
              // border: 2px solid red;
              width: fit-content;
              // background-color: $background_shade;
              display: flex;
              justify-content: center;
              align-items: center;
              flex-direction: column;
              gap: 5px;
              select {
                color: #737791;
                cursor: pointer;
                font-size: 0.9vw;
                outline: none;
                // border: 0.5px solid #737791;
                padding: 6px 12px;
                border-radius: 3px;
                // border: none;
                box-shadow: #e5ebef 0px 0px 0px 1px;
                border: none;
              }
              p {
                width: 100%;
                border: 1px solid #e5ebef;

                border-radius: 5px;
                padding: 6px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 10px;
                background-color: $background_shade;
                font-size: 0.7rem;
                color: #737791;
              }
            }
          }

          .apply-button-api {
            width: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            gap: 10px;
            .reset {
              height: fit-content;
              width: fit-content;
              border-radius: 3px;

              box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px,
                rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
              button {
                // height: 100%;
                padding: 6px 15px;
                cursor: pointer;
                border: none;
                border-radius: 3px;
                font-size: 1vw;
                background-color: $lighter_shade;
                color: #737791;
                font-weight: 500;
              }
              button:hover {
                background-color: $background_color;
                color: $background_shade;
              }
            }

            .apply_filter {
              height: fit-content;
              width: fit-content;
              border-radius: 3px;
              box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
                rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
              button {
                padding: 6px 15px;
                cursor: pointer;
                border: none;
                background-color: $lighter_shade;
                color: #5d5fef;

                border-radius: 3px;
                font-size: 1vw;
              }
              button:hover {
                background-color: $background_color;
                color: $background_shade;
              }
            }
          }
        }

        .filter-icon {
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: flex-start;
          span {
            display: flex;
            justify-content: center;
            align-items: flex-start;
            font-size: 1.5rem;
            color: #737791;
          }
        }

        // .dropdown{
        //   // border: 2px solid red;
        //   width: fit-content;
        //   background-color: $background_shade;
        //   select{
        //     color: #737791;
        //     cursor: pointer;
        //     font-size: 0.9vw;
        //     outline: none;
        //     border: 1px solid rgb(57, 56, 56);
        //     padding: 8px 12px;
        //     border-radius: 3px;
        //     border: none;
        //     box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;

        //   }
        // }

        .edit_button {
          height: fit-content;
          width: fit-content;
          border-radius: 3px;
          box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
            rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
          button {
            padding: 8px 15px;
            cursor: pointer;
            border: none;
            background-color: $lighter_shade;
            color: #737791;

            border-radius: 3px;
            font-size: 1vw;
          }
        }

        .upload_button {
          height: fit-content;
          width: fit-content;
          // box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
          //   rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
          border-radius: 3px;
          box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
          background-color: $lighter_shade;
        }
        .upload_report-button {
          height: fit-content;
          width: fit-content;
          box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
          // box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
          //   rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
          border-radius: 3px;
          background-color: $lighter_shade;
        }
        .report {
          height: fit-content;
          width: fit-content;
          // box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
          //   rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
          box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
          border-radius: 3px;
          cursor: pointer;

          button {
            border-radius: 10px;
            padding: 3px;
            border: none;
            background-color: $lighter_shade;
            color: #737791;

            #analytic {
              font-size: 1.3rem;
            }
          }
        }
      }
      .table {
        width: 100%;
        height: calc(90% - 45px);
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        gap: 2px;
        padding: 7px 15px;
        overflow: hidden;
        // border: 2px solid red;
      

        table {
          background-color: white;
          width: 100%;
          border-color: #e8e8e8;
         
          // table-layout: fixed;
          // overflow: hidden;
          border-radius: 5px;
          border-collapse: collapse;
          box-shadow: #dfe5f9 0px 1px 3px 0px, #dfe5f9 0px 0px 0px 1px;
          thead {
            height: 10%;
            // border: 2px solid red;
            background-color: #f9f9f9;
            tr .name-test-case {
              max-width: 20%;
              height: 100%;
              text-overflow: ellipsis;
            }

            tr {
              .select {
                p {
                  display: inline-block;
                }
              }
              th {
                padding: 1vh 1vw;
                font-size: 0.85vw;
                font-weight: 500;
                input {
                  width: 100%;
                  height: 100%;
                  padding: 0px 5px;
                  border: none;
                  outline: none;
                  font-size: 0.1vw;
                }

                // .dropdown{

                //   select{
                //     font-size: 0.9vw;
                //     outline: none;
                //     border: 1px solid rgb(57, 56, 56);
                //     padding: 4px 8px;
                //     border-radius: 3px;

                //   }
                // }
              }
            }

            #row-one {
              color: #121c2d;
              // background-color: $background_shade;
              // background-color: #FAFAF6;
              padding: 20px;

              th {
                padding: 1vw;
              }
            }
          }

          tbody {
       
            width: 100%;
            height: fit-content;
            tr {
              width: 35px;
              td {
                padding: 1vh 1vw;
                text-align: justify;

                font-size: 0.9vw;
                // height: fit-content;
               
              }
              .select {
                p {
                  display: inline-block;
                }
              }

              #name {
                cursor: pointer;
              }
            }
          }
        }
      }
      .pagination {
        width: 100%;
        height: 10%;
        // border: 2px solid red;
      }
    }
  }

  // .add-test-case-form{
  //   border: 2px solid red;
  //   position: absolute;
  //   width: 100vw;
  //   height: 100vh;
  //   z-index: 10000000;

  // }
}

@media screen and (max-width: 2559px) and (min-width: 1920px) {
  .page {
    .list {
      .inner_list {
        // padding-top: 80px;
        .filter_button {
          height: 70px;
        }
        .table {
          height: calc(100% - 70px);

          table {
            width: 100%;

            thead {
              height: 70px;
              tr {
                th {
                  padding: 1.8vh 1vw;
                }
              }
            }

            tbody {
              width: 100%;
              tr {
                height: 50px;
                td {
                  padding: 1.8vh 1vw;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (min-width: 2560px) {
  .page {
    .list {
      .inner_list {
        // padding-top: 80px;
        .filter_button {
          height: 90px;
        }
        .table {
          height: calc(100% - 00px);

          table {
            width: 100%;

            thead {
              height: 120px;
              tr {
                th {
                  padding: 1.8vh 1vw;
                }
              }
            }

            tbody {
              width: 100%;
              tr {
                height: 103px;
                td {
                  padding: 1.8vh 1vw;
                }
              }
            }
          }
        }
      }
    }
  }
}
