.details_script{
    width: 100%;
    height: fit-content;
    // padding: 10px;
   
    font-size: 1vw;
    .inner_details{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        // padding: 10px;
        gap: 15px;
      
        .script_heading{
            width: 100%;
            height: fit-content;
            padding: 4px 0px;
            display: flex;
            justify-content: space-between;
            align-items: center;
           p{
            font-size: 0.9rem;
            font-weight: 500;
            width: 100%;
            color: #3f4d67;
           
            font-weight: 500;
           }
        }
        .script_info{
            width: 100%;
            height: fit-content;
            display: flex;
            justify-content: center;
            align-items: flex-start;
            flex-direction: column;
            gap: 10px;

            .common_s{
              width: 100%;
             
              padding: 0px 10px;
              p{
                
                    display: inline-block;
                    width: 100%;
                    color: #485567;
                    font-size: 0.8rem;
                  
                
              }

              textarea{
                box-shadow: #dae1e9 0px 0px 1px 0px, #dae1e9 0px 0px 2px 0px;
                width: 100%;
                border-radius: 3px;
                padding: 5px;
                color: #485567;
                font-size: 0.8rem;
                border: 1px solid #dae1e9;
              }
              textarea:focus{
                  border: 1px solid rgb(93, 114, 144);
                  box-shadow: rgb(93, 114, 144) 0px 0px 2px 0px, rgb(93, 114, 144)  0px 0px 1px 0px;
                  outline: none;
              }
        }
        }

    }
}

@media screen and (min-wdith:2560px)  {
    
    .details_script{

        .details_script{

            .inner_details{
    
                .script_heading{
                   
                    p{
                        font-size: 1.2rem;
                        
                    }
                }
    
                .script_info{
                    .common_s{
                        p{
                            font-size: 1rem;
                        }
    
                        textarea{
                            font-size: 1rem;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 2559px) and (min-width: 1920px){
    .details_script{

        .inner_details{

            .script_heading{
               
                p{
                    font-size: 1.2rem;
                    
                }
            }

            .script_info{
                .common_s{
                    p{
                        font-size: 1rem;
                    }

                    textarea{
                        font-size: 1rem;
                    }
                }
            }
        }
    }
}