.filter-panel {
  // width: 500px;
  height: 100vh;
  // border: 2px solid red;
  position: fixed;
  right: 0px;
  top: 0px;
  background-color: white;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: right 0.5s ease-in-out; /* Smooth transition */
  .top-section-panel {
    height: 46px;
    width: 100%;
    background: rgb(73, 162, 178);
    background: linear-gradient(
      81deg,
      rgba(73, 162, 178, 1) 59%,
      rgba(84, 186, 134, 0.5944502801120448) 100%
    );
    padding: 0px 10px;
    .panel-heading {
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      p {
        font-size: 0.9rem;
        font-weight: 500;
        color: white;
      }
    }
  }
  .bottom-side-panel {
    width: 100%;
    height: calc(100% - 40px);
    display: flex;
    justify-content: center;
    align-items: center;
    .left-panel {
      height: 100%;
      width: 50%;
      padding: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      gap: 15px;

      #left-heading {
        width: 100%;
        text-align: left;
        p {
          font-size: 0.85rem;
          font-weight: 500;
          color: #374659;
        }
      }
      .option-section{
        width: 100%;
        height: 90%;
        
        position: relative;
        padding: 10px 5px;
        .mssg{
          width: 100%;
         
          text-align: center;
          p{
            color: #6c7684;
            font-size: 0.8rem;
            font-weight: 400;
          }
        }
        
        .option-list{
        
          width: 100%;
          height: 200px;
          position: absolute;
          right: 0px;
          transition: right 0.5s ease-in-out;
          display: flex;
          justify-content: flex-start;
          align-items: center;
          flex-direction: column;
          gap: 10px;
          .collection{
            width: 100%;
            display: flex;
          justify-content: flex-start;
          align-items: center;
          flex-direction: column;
          gap: 5px;
          p{
            width: 100%;
            border-radius: 4px;
            border: 1px solid #d7dee7;
            padding: 8px 8px;
            color: #6c7684;
            cursor: pointer;
            font-size: 0.8rem;
          }
          p:hover{
            border: 1px solid #6cd1e4;
            color: #6cd1e4;

          }
          }
        }

        .open-option{
            width: 100%;
            right: 0px;
        }

        .close-option{
           right: -110%;
           z-index: -1;
        }
      }
      .apply-button{
        width: 100%;
        text-align: right;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        gap: 10px;
        button{
          padding: 5px 8px;
          font-size: 0.9rem;
          font-weight: 500;
          border: none;
          // border:1px solid #6cd1e4;
          color: white;
          border-radius: 4px;
          background: rgb(73, 162, 178);
          cursor: pointer;
        }
      }

    }
  
    .right-panel {
      height: 100%;
      width: 50%;
      border-left: 1.5px solid #d7dee7;
      padding: 10px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
      gap: 15px;
      z-index: 2;
      background-color: white;

      #right-heading {
        width: 100%;
        text-align: left;
        p {
          font-size: 0.85rem;
          font-weight: 500;
          color: #374659;
        }
      }
      .filter-box {
        padding: 0px 5px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 5px;

        .filter-select-option {
          width: 100%;
          padding: 10px 10px;
          cursor: pointer;
          
          p {
            width: 100%;
            font-size: 0.8rem;
            font-weight: 450;
        
            color: #6c7684;
          
            // border: 2px solid red;
          }
          
        }
        .filter-select-option:hover{
          //  border: 1px solid #6c7684;
           border-radius: 4px;
           box-shadow: 0 1px 3px 0 rgba(0,0,0,0.1), 0 1px 2px -1px rgba(0,0,0,0.1);
        }
      }
    }
  }
}
.open-panel {
  width: 500px;
  right: 0px;
}

.close-panel {
  width: 500px;
  right: -500px;
}


@media screen and (max-width:2559px) and (min-width:1920px) {
   .filter-panel{
      
    .top-section-panel{
      height: 70px;
    }
   }
}

@media screen and (min-width:2560px) {
  .filter-panel{
      
    .top-section-panel{
      height: 70px;
    }
   }
}