.para_boxx {
  width: 100%;
  height: 100%;
  padding: 10px;
  position: relative;
  background-color: white;
  z-index: 5;
 
  .inner_parabox {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    .natco_para {
      width: 100%;
      height: fit-content;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      gap: 5px;
      p {
        width: 100%;
        font-size: 0.8rem;
        border-bottom: 1px solid black;
      }
      .checkbox {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        label {
          font-size: 0.7rem;

          display: flex;
          justify-content: center;
          align-items: center;
          gap: 2px;
        }
      }
    }

    .testCase_para {
      width: 100%;
      max-height: 40%;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      gap: 5px;
      
     
      p {
        width: 100%;
        font-size: 0.8rem;
        border-bottom: 1px solid black;
      }
      .radio {
        width: 100%;
        height: 100%;
        overflow: scroll;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        label {
          font-size: 0.7rem;

          display: flex;
          justify-content: center;
          align-items: center;
          gap: 2px;
        }
      }
    }
    .radio::-webkit-scrollbar{
      width: 4px;
    }

    .device_para {
      width: 100%;
      height: fit-content;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      gap: 5px;

      p {
        width: 100%;
        font-size: 0.8rem;
        border-bottom: 1px solid black;
      }

      .checkbox {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        label {
          font-size: 0.7rem;

          display: flex;
          justify-content: center;
          align-items: center;
          gap: 2px;
        }
      }
    }

    .Build_para {
      width: 100%;
      height: fit-content;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      gap: 5px;

      p {
        width: 100%;
        font-size: 0.8rem;
        border-bottom: 1px solid black;
      }
      .checkbox {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        label {
          font-size: 0.7rem;

          display: flex;
          justify-content: center;
          align-items: center;
          gap: 2px;
        }
      }
    }
  }
  
}


.closed_para_boxx{
  width: 5%;
  height: 100%;
  background-color: white;

  .inner_parabox{
    display: none;
  }
}
