.proportion {
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;

  .innerChart {
    width: 90%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    margin-top: 50px;

    .display {
      width: 100%;
      display: flex;

      gap: 20px;
      #heading {
        width: 10%;
        // border: 2px solid red;
        display: block;
      }
      .noddes {
        width: 90%;
        overflow: scroll;
        // border: 2px solid red;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 20px;
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;
        .node {
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 10px;
          span {
            width: 10px;
            height: 10px;
            display: inline-block;
            border-radius: 50%;
          }
        }
      }
      /* width */
::-webkit-scrollbar {
    width: 2px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }
    }

    .chart {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      .portion {
        width: 100%;
        display: flex;
        .block {
          padding: 3px 5px;
          position: relative;
          cursor: pointer;
          p {
            color: white;
          }
          .hover {
            padding: 10px;
            min-width: 200%;

            height: fit-content;
            border-radius: 3px;
            position: absolute;
            left: 100%;
            top: 0;
            z-index: 10;
            background-color: white;
            box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
              rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
            p {
              color: black;
            }
            .arrow {
              width: 10px;
              height: 10px;

              position: absolute;
              z-index: 0;
              left: -5px;
              top: 5%;
              transform: rotate(45deg);
              background-color: white;
            }
          }
        }
      }
    }
  }
}


      /* width */
      .noddes::-webkit-scrollbar {
        display: none;
      }
      
      /* Hide scrollbar for IE, Edge and Firefox */
      .example {
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
      }

