.list-table {
  width: 100%;
  height: 100%;
  background-color: white;
  border-color: #e8e8e8;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  border-radius: 5px;
  box-shadow: #dfe5f9 0px 1px 3px 0px, #dfe5f9 0px 0px 0px 1px;
  overflow: hidden;
  .head {
    width: 100%;
    height: 10%;
    background-color: #f9f9f9;
    color: #121c2d;
    .head-row {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #121c2d;

      .sl {
        width: 5%;
        //   border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }

      .id {
        width: 10%;
        //   border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      .name {
        width: 30%;
        height: 100%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      .automation-status {
        width: 15%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      .priority {
        width: 19%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      .status {
        width: 10%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
      .last-run {
        width: 20%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.85vw;
        font-weight: 500;
      }
    }
  }

  .body {
    width: 100%;
    height: 90%;
    // border: 2px solid green;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    .body-row {
      width: 100%;
      height: 10%;
      // border: 2px solid red;
      display: flex;
      justify-content: center;
      align-items: center;
      .sl {
        width: 5%;
        //   border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.9vw;
        color: #737791;
      }

      .id {
        width: 10%;
        //   border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.9vw;
        color: #2f63c7;
        cursor: pointer;
      }
      .name {
        width: 30%;
        height: 100%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.9vw;
        color: #737791;
      }
      .automation-status {
        width: 15%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.9vw;
        color: #737791;
      }
      .priority {
        width: 19%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.9vw;
        color: #737791;
      }
      .status {
        width: 10%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.9vw;
        color: #737791;
      }
      .last-run {
        width: 20%;
        // border: 2px solid red;
        height: 100%;
        padding: 5px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        border-right: 1px solid #e8e8e8;
        font-size: 0.9vw;
        color: #737791;
      }
    }
  }

  .body {
    .last-run {
      width: 20%;
      // border: 2px solid red;
      height: 100%;
      padding: 5px 10px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      border-bottom: 1px solid #e8e8e8;
      border-right: 1px solid #e8e8e8;
      font-size: 0.85vw;
      color: #737791;
    
      overflow: hidden;
      .result-dots {
        display: flex;
        justify-content: space-between;
        align-items: center;
   
        width: 100%;
      }

      .dot {
        width: 5px;
        height: 20px;
        // border-radius: 50%;
        display: inline-block;
        border-radius: 1px;
      }

      .dot.pass {
        background-color: #b5e380;
      }

      .dot.error {
        background-color: #DDDDDD;
      }

      .dot.fail {
        background-color: #fa7070;
      }
    }
  }
}



