$background_color:#124076;
$lighter_shade:#fcf4f7;

.Login {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
  background-color: whitesmoke;

  .innowave_logo_brand{
    position: absolute;
    z-index: 1000;
    left: 10px;
    top: 10px;
    img{
      width: 50px;
      height: 50px;
    }
  }


  .inner_login {
    width: 30%;
    z-index: 999;
    height: 400px;
    border-radius: 20px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    // padding: 40px 20px;
    flex-direction: column;
    gap: 45px;
    position: relative;

    .error_message {
      position: absolute;
      width: 300px;
      top: 20px;
      right: 0px;
      z-index: 1000;

      border-radius: 5px;
      padding: 20px;
      background-color: white;
      box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
        rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
      transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
      transform: translateX(310px);
      opacity: 1;

      p {
        color: gray;
        font-size: 1rem;
      }
    }

    .error_message_close {
      width: 300px;
      position: absolute;
      top: 18px;
      z-index: -1000;
      translate: 0px;
      transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
      border-radius: 5px;
      padding: 20px;
      background-color: white;
      box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
        rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
      transform: translateX(0);

      p {
        color: grey;
        font-size: 1rem;
      }
    }

    .login_form_heading {
      width: 100%;

      height: 100%;
      z-index: 100000;
      border-radius: 20px;
      background-color: white;
      padding: 40px 20px;
      display: flex;
      justify-content: flex-end;
      align-items: center;

      flex-direction: column;
      gap: 45px;

      .user_icon {
        width: 100%;
        position: absolute;
        // border: 2px solid red;
        top: -60px;
        display: flex;
        justify-content: center;
        align-items: center;

        .icon {
          // border: 2px solid black;
          padding: 20px;
          border-radius: 50%;
          font-size: 5rem;
          display: flex;
          justify-content: center;
          align-items: center;
          background-color: $background_color;
          color: white;
        }
      }

      .heading {
        color: $background_color;
        text-align: center;
      }

      .login_form {
        width: 90%;

        form {
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          gap: 20px;

          .password_section {
            display: flex;
            width: 100%;
            box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;

            label,
            input {
              height: 35px;
            }

            .password::placeholder {
              color: grey;
            }
            label {
              width: 10%;
              display: flex;
              justify-content: center;
              align-items: center;
              background-color: $background_color;
              color: white;
            }
            input {
              width: 90%;
              outline: none;
              border: none;
              background-color: whitesmoke;
              padding: 5px;
              font-size: larger;
              color: grey;
            }
          }

          .email_section {
            display: flex;
            width: 100%;
            box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;

            label,
            input {
              height: 35px;
            }
            .email::placeholder {
              color: grey;
            }
            label {
              width: 10%;
              display: flex;
              justify-content: center;
              align-items: center;
              background-color: $background_color;
              color: white;
            }
            input {
              width: 90%;
              outline: none;
              border: none;
              background-color: whitesmoke;
              padding: 5px;
              font-size: larger;
              color: grey;
            }
          }

          .login_button {
            width: 100%;
            box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
            button {
              width: 100%;
              height: 40px;
              border: none;
              color: white;
              font-size: 1.3rem;
              background-color: $background_color;
              cursor: pointer;
            }
          }
          .login_button:hover {
            box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
              rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
          }
        }

        p {
          color: $background_color;

          .link_signup {
            text-decoration: none;
            color: $background_color;
            font-weight: 700;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .Login {
    .inner_login {
      width: 70%;
      height: 420px;
    }
  }
}

@media screen and (min-width: 601px) and (max-width: 950px) {
  .Login {
    .inner_login {
      width: 55%;
      height: 430px;
    }
  }
}

@media screen and (min-width: 951px) and (max-width: 1200px) {
  .Login {
    .inner_login {
      width: 40%;
    }
  }
}

@media screen and (min-width: 2560px) {
  .Login {
    .left_circle {
      width: 800px;
      height: 800px;
    }

    .right_circle {
      width: 800px;
      height: 800px;
    }

    .inner_login {
      height: 400px;
    }
  }
}

@media screen and (max-width: 2559px) and (min-width: 1920px) {
  .Login {
    .left_circle {
      width: 650px;
      height: 650px;
    }

    .right_circle {
      width: 650px;
      height: 650px;
    }

    .inner_login {
      height: 400px;
    }
  }
}
